import type { Meta, StoryFn } from '@storybook/web-components';

import { html } from 'lit';

import { ButtonComponentWC, ButtonSize, ButtonVariant } from '@tiger-analytics/ui/button';

export default {
  title: 'Button/ta-button',
  component: 'ta-button',
} as Meta;

const description = 'Sets focus on the button after 4 second';

const Template: StoryFn = () => {
  setTimeout(() => {
    const buttonComponent = document.getElementById('test-button-1') as ButtonComponentWC;
    if (buttonComponent) {
      buttonComponent.focus();
    }
  }, 4000);

  return html`
    <ta-button
      id="test-button-1"
      .variant="${ButtonVariant.Secondary}"
      @click="${() => alert('Button clicked!')}">
      Button</ta-button
    >
    <ta-grid-container .showSideMargin="${false}">
      <ta-grid-column
        ><div class="story-title">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</div></ta-grid-column
      >
      <ta-grid-column><div class="story-description">${description}</div></ta-grid-column>
    </ta-grid-container>
  `;
};

export const Example = Template.bind({});
Example.args = {};
